"use client";

import { useState } from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  Button,
  Grid,
  IconButton,
  Typography,
  Box,
} from "@mui/material";
import { Close } from "@mui/icons-material";

type PetFormData = {
  name: string;
  type: string;
  breed: string;
  gender: string;
  size: string;
  ageGroup: string;
  description: string;
  imageUrls: string[];
};

type AddPetModalProps = {
  open: boolean;
  onClose: () => void;
  onSubmit: (petData: PetFormData) => Promise<void>;
};

export default function AddPetModal({
  open,
  onClose,
  onSubmit,
}: AddPetModalProps) {
  const [formData, setFormData] = useState<PetFormData>({
    name: "",
    type: "",
    breed: "",
    gender: "",
    size: "",
    ageGroup: "",
    description: "",
    imageUrls: [],
  });
  const [currentImageUrl, setCurrentImageUrl] = useState("");
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleInputChange = (field: keyof PetFormData, value: string) => {
    setFormData((prev) => ({ ...prev, [field]: value }));
  };

  const addImageUrl = () => {
    if (
      currentImageUrl.trim() &&
      !formData.imageUrls.includes(currentImageUrl.trim())
    ) {
      setFormData((prev) => ({
        ...prev,
        imageUrls: [...prev.imageUrls, currentImageUrl.trim()],
      }));
      setCurrentImageUrl("");
    }
  };

  const removeImageUrl = (urlToRemove: string) => {
    setFormData((prev) => ({
      ...prev,
      imageUrls: prev.imageUrls.filter((url) => url !== urlToRemove),
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);

    try {
      await onSubmit(formData);
      setFormData({
        name: "",
        type: "",
        breed: "",
        gender: "",
        size: "",
        ageGroup: "",
        description: "",
        imageUrls: [],
      });
      setCurrentImageUrl("");
      onClose();
    } catch (error) {
      console.error("Error submitting pet:", error);
    } finally {
      setIsSubmitting(false);
    }
  };

  const isFormValid =
    formData.name &&
    formData.type &&
    formData.gender &&
    formData.size &&
    formData.ageGroup;

  return (
    <Dialog open={open} onClose={onClose} maxWidth="md" fullWidth>
      <DialogTitle>
        <Box display="flex" justifyContent="space-between" alignItems="center">
          Add New Pet
          <IconButton onClick={onClose}>
            <Close />
          </IconButton>
        </Box>
      </DialogTitle>

      <form onSubmit={handleSubmit}>
        <DialogContent>
          <Grid container spacing={3}>
            <Grid size={{ xs: 12, sm: 6 }}>
              <TextField
                required
                fullWidth
                label="Pet Name"
                value={formData.name}
                onChange={(e) => handleInputChange("name", e.target.value)}
              />
            </Grid>

            <Grid size={{ xs: 12, sm: 6 }}>
              <FormControl fullWidth required>
                <InputLabel>Type</InputLabel>
                <Select
                  value={formData.type}
                  label="Type"
                  onChange={(e) => handleInputChange("type", e.target.value)}
                >
                  {PET_TYPES.map((type) => (
                    <MenuItem key={type.value} value={type.value}>
                      {type.label}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>

            <Grid size={{ xs: 12, sm: 6 }}>
              <TextField
                fullWidth
                label="Breed"
                value={formData.breed}
                onChange={(e) => handleInputChange("breed", e.target.value)}
                placeholder="e.g., Golden Retriever, Persian"
              />
            </Grid>

            <Grid size={{ xs: 12, sm: 6 }}>
              <FormControl fullWidth required>
                <InputLabel>Gender</InputLabel>
                <Select
                  value={formData.gender}
                  label="Gender"
                  onChange={(e) => handleInputChange("gender", e.target.value)}
                >
                  {GENDERS.map((gender) => (
                    <MenuItem key={gender.value} value={gender.value}>
                      {gender.label}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>

            <Grid size={{ xs: 12, sm: 6 }}>
              <FormControl fullWidth required>
                <InputLabel>Size</InputLabel>
                <Select
                  value={formData.size}
                  label="Size"
                  onChange={(e) => handleInputChange("size", e.target.value)}
                >
                  {SIZES.map((size) => (
                    <MenuItem key={size.value} value={size.value}>
                      {size.label}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>

            <Grid size={{ xs: 12, sm: 6 }}>
              <FormControl fullWidth required>
                <InputLabel>Age Group</InputLabel>
                <Select
                  value={formData.ageGroup}
                  label="Age Group"
                  onChange={(e) =>
                    handleInputChange("ageGroup", e.target.value)
                  }
                >
                  {AGE_GROUPS.map((age) => (
                    <MenuItem key={age.value} value={age.value}>
                      {age.label}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>

            <Grid size={{ xs: 12 }}>
              <TextField
                fullWidth
                multiline
                rows={4}
                label="Description"
                value={formData.description}
                onChange={(e) =>
                  handleInputChange("description", e.target.value)
                }
                placeholder="Tell potential adopters about this pet's personality, habits, medical needs, etc."
              />
            </Grid>

            <Grid size={{ xs: 12 }}>
              <Box>
                <Typography variant="subtitle2" gutterBottom>
                  Photo URLs
                </Typography>
                <Box display="flex" gap={1} mb={2}>
                  <TextField
                    fullWidth
                    placeholder="Enter image URL"
                    value={currentImageUrl}
                    onChange={(e) => setCurrentImageUrl(e.target.value)}
                    onKeyPress={(e) => {
                      if (e.key === "Enter") {
                        e.preventDefault();
                        addImageUrl();
                      }
                    }}
                  />
                  <Button
                    variant="outlined"
                    onClick={addImageUrl}
                    disabled={!currentImageUrl.trim()}
                    sx={{ minWidth: "auto", px: 2 }}
                  >
                    Add
                  </Button>
                </Box>

                {formData.imageUrls.length > 0 && (
                  <Box>
                    <Typography
                      variant="body2"
                      color="text.secondary"
                      gutterBottom
                    >
                      Added photos ({formData.imageUrls.length}):
                    </Typography>
                    <Box display="flex" flexDirection="column" gap={1}>
                      {formData.imageUrls.map((url, index) => (
                        <Box
                          key={index}
                          display="flex"
                          alignItems="center"
                          justifyContent="space-between"
                          p={1}
                          border="1px solid #e0e0e0"
                          borderRadius={1}
                        >
                          <Typography
                            variant="body2"
                            sx={{
                              overflow: "hidden",
                              textOverflow: "ellipsis",
                              whiteSpace: "nowrap",
                              maxWidth: "300px",
                            }}
                          >
                            {url}
                          </Typography>
                          <IconButton
                            size="small"
                            onClick={() => removeImageUrl(url)}
                            color="error"
                          >
                            <Close fontSize="small" />
                          </IconButton>
                        </Box>
                      ))}
                    </Box>
                  </Box>
                )}
              </Box>
            </Grid>
          </Grid>
        </DialogContent>

        <DialogActions>
          <Button onClick={onClose}>Cancel</Button>
          <Button
            type="submit"
            variant="contained"
            disabled={!isFormValid || isSubmitting}
            sx={{
              backgroundColor: "#ed9426",
              "&:hover": { backgroundColor: "#d4841f" },
            }}
          >
            {isSubmitting ? "Adding Pet..." : "Add Pet"}
          </Button>
        </DialogActions>
      </form>
    </Dialog>
  );
}
